/* Homepage.js區塊樣式 */
.content-section {
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.home-page-links {
  list-style-type: none;
  padding: 0;
}

.home-page-links li {
  margin-bottom: var(--spacing-medium);
}

.link-button {
  background-color: var(--secondary-color);
  color: white;
  padding: var(--spacing-small) var(--spacing-medium);
  border: none;
  border-radius: var(--border-radius);
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s;
  display: inline-block;
}

.link-button:hover {
  background-color: var(--accent-color);
}

.major {
  font-size: var(--font-large);
  color: var(--primary-color);
  margin-bottom: var(--spacing-medium);
  font-weight: bold;
}

/* Header.js區塊樣式 */
.header-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--spacing-large);
  background-color: var(--primary-color);
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.nav-button {
  background-color: var(--secondary-color);
  color: white;
  padding: var(--spacing-small) var(--spacing-medium);
  border: none;
  border-radius: var(--border-radius);
  margin: 0 var(--spacing-small);
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

.nav-button:hover {
  background-color: var(--accent-color);
}

:root {
  --primary-color: #3498db;  /* 藍色 */
  --secondary-color: #2ecc71;  /* 綠色 */
  --accent-color: #e74c3c;  /* 紅色 */
  --font-medium: 16px;
  --spacing-small: 8px;
  --spacing-medium: 16px;
  --spacing-large: 24px;
  --border-radius: 8px;
}

/* FinanceManager.js區塊樣式 */
.content-section {
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 使用說明文字 */
.instructions {
  background-color: #fff3cd;
  border: 1px solid #ffeeba;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 5px;
  font-size: 16px;
  color: #856404;
}

.instructions p {
  margin: 10px 0;
}

.instructions ol {
  margin-left: 20px;
}

.instructions li {
  margin-bottom: 8px;
}

.form-group {
  margin-bottom: var(--spacing-large);
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: var(--spacing-small);
}

.form-group input {
  width: 100%;
  padding: var(--spacing-small);
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
}

/* 計算按鈕 */
.button-primary {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button-primary:hover {
  background-color: #0056b3;
}

/* 計算結果區域 */
h3 {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  margin-top: 20px;
}

p {
  font-size: 16px;
  color: #333;
}

/* 圖表區域 */
.recharts-wrapper {
  margin-top: 30px;
  text-align: center;
}

/* Calendar.js區塊樣式 */
.workday {
  background-color: var(--accent-color); /* 使用主題色的背景 */
  color: white;
  font-weight: bold;
}

.todo-section {
  margin-top: 20px;
  padding: var(--spacing-large);
  background-color: #f9f9f9;
  border-radius: var(--border-radius);
}

.form-control {
  margin-right: 10px;
  width: 80%;
}

/* AttendanceTracker.js區塊樣式 */
.button-primary {
  background-color: var(--primary-color);
  color: white;
  padding: var(--spacing-small) var(--spacing-large);
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}
.button-primary:hover {
  background-color: var(--secondary-color);
}
.table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: var(--spacing-large);
}

.table th, .table td {
  padding: var(--spacing-small);
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.table th {
  background-color: var(--primary-color);
  color: white;
  font-weight: bold;
}

.table-wrapper {
  margin-top: var(--spacing-medium);
  overflow-x: auto;
}
.content-section {
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* LeaveScheduler.js區塊樣式 */
/* 請假行事曆容器 */
.content-section {
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 日曆容器 */
.calendar-container {
  margin-top: var(--spacing-large);
}

/* 高亮選中的請假日期 */
.react-calendar__tile.highlight {
  background-color: var(--accent-color);
  color: white;
  font-weight: bold;
}

/* 請假摘要樣式 */
.vacation-summary {
  margin-top: var(--spacing-large);
}

.vacation-summary h3 {
  color: var(--primary-color);
  margin-bottom: var(--spacing-small);
}

.vacation-summary ul {
  list-style-type: none;
  padding: 0;
}

.vacation-summary li {
  font-size: var(--font-medium);
  margin-bottom: var(--spacing-small);
}

